import './App.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import axios from 'axios';
import data from './data.js';
import { lazy, Suspense, useState, useEffect, createContext } from 'react';
import { Button, Navbar, Container, Nav, Row, Col } from 'react-bootstrap';
import { Routes, Route, Link, useNavigate, Outlet } from 'react-router-dom';
import { useQuery } from 'react-query';

//     lazy   
//     
//    . => Suspense   .
const Detail = lazy(() => import('./routes/Detail.js'));
const Cart = lazy(() => import('./routes/Cart.js'));


// export let Context1 = createContext();  redux 
// <Context1.Provider value={ {stock, shoes} }> 
//   state    
// </Context1.Provider>

function App() {

  
  let [shoes, setShoes] = useState(data);
  let navigate = useNavigate();
  
  let [click, setClick] = useState(2);
  let [loading, setLoading] = useState(false);
  // let [stock, setStock] = useState([10, 11, 12]);
  
  let getItem = JSON.parse(localStorage.watched);
  
  useEffect(()=>{
    if(getItem.length == 0){
      localStorage.setItem('watched', JSON.stringify([]));
    }
  }, [])


  // react-query 
  // 1. //    
  // 2.  refetch 
  // 3.   retry 
  // 4. state   .(ajax   )
  // 5. ajax   
  // =>       .
  
  let result = useQuery('userData', ()=>{
    return axios.get('https://codingapple1.github.io/userdata.json')
    .then((a)=> {
      return a.data
    }),
    { staleTime : 2000 } // refetch   
  })



  return (
    <div className="App">

      <Navbar bg="light" variant="light">
        <Container>
          <Navbar.Brand
            onClick={() => { navigate('/') }}
            className="logo">ShopRY</Navbar.Brand>
          <Nav className="me-auto">
            <Nav.Link onClick={() => { navigate('/about') }}>About</Nav.Link>
            <Nav.Link onClick={() => { navigate('/detail') }}>Products</Nav.Link>
            <Nav.Link onClick={() => { navigate('/cart') }}>Cart</Nav.Link>
          </Nav>
          <Nav className="ms-auto">
            { result.isLoading ? '' : result.data.name }
            { result.error && 'error' }
          </Nav>
        </Container>
      </Navbar>
      
      {/* suspense    */}
      <Suspense fallback={<div></div>}> 
        <Routes>
          <Route path="/" element={
            <>
              <div className="main-bg">
                <div className="watched">
                  <p>  
                    {
                      getItem.map((a, i)=>{
                        return(
                          <span key = {i}> {a} </span>
                        )
                      })
                    }
                  </p>
                </div>
              </div>
              <button onClick={() => {
                let copy = [...shoes];
                copy.sort((a, b) =>
                  a.title < b.title ? -1 : 1
                )
                setShoes(copy);
              }}>  </button>
              <div className="card-con">
                <div className="card-con2">
                  {
                    shoes.map((a, i) => {
                      return (
                        < Card a={a} key={i} />
                      )
                    })
                  }
                </div>
              </div>
              {
                loading ? < Loading /> : null
              }
              {
                click < 4 ?
                  <button onClick={() => {
                    setClick(click + 1);
                    setLoading(true);

                    axios.get('https://codingapple1.github.io/shop/data' + click + '.json')
                      .then((result) => {
                        let newData = result.data;
                        setLoading(false);

                        let copy = [...shoes, ...newData];
                        setShoes(copy);
                      })
                      .catch(() => {
                        console.log('  ');
                        setLoading(false);
                      })

                    // axios  json  array object .
                    // fetch()  .then(data => data.json())  .

                    //  ajax   
                    // Promise.all([ axios.get('/url'), axios.get('/url') ])
                    // .then(()=>{})

                    // axios.post('/url', { name : kim })

                  }}>  </button>
                  : null
              }
            </>
          } />
            <Route
              path="/detail/:id"
              element={
                  <Detail shoes={shoes} />
              }>
            </Route>

            {/* nested route    element (Outlet  ) */}
            <Route path="/about" element={< About />}>
              <Route path="member" element={<div></div>} />
              <Route path="location" element={<div></div>} />
            </Route>

            <Route path="/event" element={< Event />}>
              <Route path="two" element={<p></p>}></Route>
            </Route>

            <Route path="/cart" element={<Cart />}></Route>
            <Route path="*" element={<div>404</div>} />
        </Routes>
      </Suspense>
    </div>
  );

}

function Loading() {
  return (
    <div className="d-flex justify-content-center">
      <div className="spinner-border" role="status"></div>
    </div>
  )
}

function Event() {
  return (
    <div>
      <h4> </h4>
      <Outlet></Outlet>
    </div>
  )
}

function About() {
  return (
    <div>
      <h4>.</h4>
      <Outlet></Outlet>
    </div>
  );
}


function Card({ a }) {
  let navigate = useNavigate();
  let src = `https://codingapple1.github.io/shop/shoes${a.id + 1}.jpg`

  return (
    <div className="product">
      <img src={src} width="80%"
        onClick={() => {
          navigate(`/detail/${a.id}`);
        }} />
      <h4>{a.title}</h4>
      <p>{a.price}</p>
    </div>
  )
}

export default App;
